<template>
  <v-container>
    <h4 class="my-4">
        <span class="border-s">{{ heading.subcat_name }} In {{ heading.city }}</span>
      </h4>
    <v-row>
      <v-col cols="12" md="3">
       <!-- <CatSidebar /> -->
              <div class="mobile">
                <div class="d-flex justify-content-between">
                  <v-btn small @click="price">Price</v-btn>
                  <v-btn small @click="deals">Deals Near By</v-btn>
                  <v-btn small @click="category">Change Category</v-btn>
                </div>
              </div>
              <div class="catside shadow desktop">
                <div class="h5 sec1">Near By Business</div>
                <v-range-slider v-model="range1" :max="max1" :min="min1" class="align-center"></v-range-slider>
                  <div class="text-center small">{{ range1[0] }} Km - {{ range1[1] }} Km</div>
                <v-btn color="success" class="my-2">Go</v-btn>
              </div>
              <v-dialog v-model="dialog1" persistent>
                <v-card>
                  <span class="d-flex justify-content-between">
                      <v-card-title>Price</v-card-title>
                      <v-icon @click="dialog1 = false" class="me-3">mdi-close</v-icon>
                  </span>
                  <hr class="m-0">
                  <v-card-text>
                    <v-range-slider
                        v-model="range" :max="max" :min="min" class="align-center"></v-range-slider>
                        <span class="d-flex">
                          <input :value="range[0]" class="me-2 form-control text-center"  @change="$set(range, 0, $event)">
                          <input :value="range[1]" class="me-2 form-control text-center"  @change="$set(range, 1, $event)">
                        </span>
                  </v-card-text>
                  <div class="text-center">
                    <b-button class="mb-3" variant="success" type="submit">Go</b-button>
                  </div>
                </v-card>
              </v-dialog>
              <v-dialog v-model="dialog2" persistent>
                <v-card>
                  <span class="d-flex justify-content-between">
                      <v-card-title>Deals Radar</v-card-title>
                      <v-icon @click="dialog2 = false" class="me-3">mdi-close</v-icon>
                  </span>
                  <hr class="m-0">
                  <v-card-text>
                    <v-range-slider v-model="range1" :max="max1" :min="min1" class="align-center"></v-range-slider>
                    <div class="text-center h6 link-dark">{{ range1[0] }} Km - {{ range1[1] }} Km</div>
                  </v-card-text>
                  <div class="text-center">
                    <b-button class="mb-3" variant="success" type="submit">Go</b-button>
                  </div>
                </v-card>
              </v-dialog>
              <v-dialog v-model="dialog3" persistent class="p-2">
                <v-card>
                  <span class="d-flex justify-content-between">
                      <v-card-title>Change Category</v-card-title>
                      <v-icon @click="dialog3 = false" class="me-3">mdi-close</v-icon>
                  </span>
                  <hr class="m-0">
                  <v-card-text class="pb-0">
                    <v-autocomplete solo dense :items="cats" label="Category" class="mt-3" placeholder="Category"></v-autocomplete>
                  </v-card-text>
                  <div class="text-center">
                    <b-button class="mb-3" variant="success" type="submit">Go</b-button>
                  </div>
                </v-card>
              </v-dialog>
      </v-col>
      <v-col cols="12" md="9">
        <div class="mobile" v-if="mobilelists.length > 1">
          <v-card v-for="(MbusinesList, i) in mobilelists" :key="i" :perPage="perPage" class="item mitem my-2 mb-7">
            <div class="small d-flex">
              <div class="mimg">
                <router-link :to="{name:'businessprofile',params:{id:MbusinesList.user_id, uname:MbusinesList.name}}">
                  <v-img v-if="MbusinesList.user_avatar!=null" :src="(url+MbusinesList.user_avatar)" height="120"></v-img>
                  <v-img v-if="MbusinesList.user_avatar==null" :src="('https://placehold.co/600x400?text='+MbusinesList.name)" height="120"></v-img>
                </router-link>
              </div>
              <div class="mdetails">
                <router-link :to="{name:'businessprofile',params:{id:MbusinesList.user_id, uname:MbusinesList.name}}" class="mtitle text-decoration-none link-dark">
                  {{ MbusinesList.name }}
                </router-link>
                <div>Add to Wishlist<v-icon>mdi-heart-outline</v-icon></div>
                <div class="ratings d-flex align-items-center">
                  <v-rating :value=MbusinesList.rating_avrage color="amber" dense half-increments size="20"></v-rating>
                  <div class="white--text badge bg-dark ms-2">
                    {{ MbusinesList.total_reviews }}
                  </div>
                </div>   
                <div class="address">
                  <div v-if="MbusinesList.address!=null">
                        {{ MbusinesList.address }}, 
                    </div>{{ MbusinesList.city }}, {{ MbusinesList.state }}
                  </div>
                <router-link :to="{name:'businessprofile',params:{id:MbusinesList.user_id, uname:MbusinesList.name}}" class="cerd">Read More</router-link>
              </div>
              <div class="mcontact">
                <a :href="('https://wa.me/'+MbusinesList.mobile_phone+'?text=rf')" target="_blank">
                  <v-btn fab small color="success" class="mb-5"><v-icon>mdi-whatsapp</v-icon></v-btn>
                </a>
                <a :href="('tel:'+ MbusinesList.mobile_phone)">
                  <v-btn fab small color="primary"><v-icon>mdi-phone</v-icon></v-btn>
                </a>
              </div>
            </div>
          </v-card>
          <v-pagination v-if="mobilelists.length > 1" class="pagination" v-model="page" :length="Math.ceil(businessList.length/perPage)" circle></v-pagination>
        </div>
        <div class="desktop" v-if="rlists.length > 1">
          <v-card v-for="(businesList, i) in rlists" :key="i" :perPage="perPage">
            <v-row class="mb-3">
              <v-col cols="12" md="4" class="py-0">
                <router-link :to="{name:'businessprofile',params:{id:businesList.user_id, uname:businesList.name}}">
                    <v-img v-if="businesList.user_avatar!=null" :src="(url+businesList.user_avatar)" cover height="220"></v-img>
                    <v-img v-if="businesList.user_avatar==null" :src="('https://placehold.co/600x400?text='+businesList.name)" cover height="220"></v-img>
                </router-link>
              </v-col>
              <v-col cols="12" md="8">
                <v-row>
                  <v-col cols="12" md="8">
                    <v-card-text class="">
                      <div class="h4">
                        <router-link :to="{name:'businessprofile',params:{id:businesList.user_id, uname:businesList.name}}" class="text-decoration-none link-dark">{{ businesList.name }}</router-link>
                      </div>
                      <div>Add to Wishlist<v-icon>mdi-heart-outline</v-icon></div>
                      <div class="ratings align-center d-flex">
                        <v-rating :value=businesList.rating_avrage color="amber" dense half-increments size="20">
                        </v-rating>
                        <div class="white--text badge bg-dark ms-2">
                          {{ businesList.total_reviews }}
                        </div>
                      </div>
                      <div class="address">
                        <v-icon small>mdi-map-marker</v-icon>  
                        <div v-if="businesList.address!=null">
                              {{ businesList.address }}, 
                          </div>{{ businesList.city }}, {{ businesList.state }}
                        <router-link :to="{name:'businessprofile',params:{id:businesList.user_id, uname:businesList.name}}" class="cred">Read More</router-link>
                      </div>
                    </v-card-text>
                  </v-col>

                  <v-col cols="12" md="4">
                    <v-card-text>
                      <a :href="('tel:'+ businesList.mobile_phone)">
                        <v-btn small color="primary" class="me-1 mt-1">
                          <v-icon small>mdi-phone</v-icon>{{ businesList.mobile_phone }}
                        </v-btn>
                      </a>
                      <a :href="('https://wa.me/'+businesList.mobile_phone+'?text=rf')" target="_blank">
                        <v-btn small color="success" class="me-1 mt-1">
                          <v-icon small>mdi-whatsapp</v-icon>{{ businesList.mobile_phone }}
                        </v-btn>
                      </a>
                    </v-card-text>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
          </v-card>
          <v-pagination v-if="rlists.length > 1" class="pagination" v-model="page" :length="Math.ceil(businessList.length/perPage)" circle></v-pagination>
        </div>
        <div v-else class="text-center">
          <!-- <v-skeleton-loader
          class="mx-auto"
          max-width="800"
          type="card"
          ></v-skeleton-loader> -->
          <v-img cover :src="('https://dummyimage.com/1200x600/ffffff/000000.png&text=Business not found of '+heading.subcat_name+' In '+heading.city)"></v-img>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from "axios";
// import CatSidebar from "@/views/CatSidebar";
export default {
  name: "CategorycitySearch",
  // components: {CatSidebar},
  props:{
    city_id:Number,
    sub_cat_id:Number,
  },

  data() {
        return {
            url: 'https://bizlx.itechvision.in',
            heading: [],
            page:1,
            perPage:10,
            businessList: [],

            dialog1: false,
            dialog2: false,
            dialog3: false,
            catvalue:null,
            selected: null,
            min: 0,
            max: 120000,
            price_from: [],
            price_to: [],
            range: [],
            range1: [0, 100],
            min1: 0,
            max1: 100,
        };
    },
    mounted() {
    this.getAllCityCategory();
  },
  methods:{
    price() {
      this.dialog1 = true;
    },
    deals() {
      this.dialog2 = true;
    },
    category() {
      this.dialog3 = true;
    },
    getAllCityCategory(){
        axios.get('popular/cat/cities/business/'+this.sub_cat_id+'/'+this.city_id)
          .then((resp)=>{
            this.heading = resp.data.cityDetails;
            this.businessList = resp.data.businessList;
        });
    },
  },
  computed:{
    mobilelists(){
      return this.businessList.slice((this.page - 1)*this.perPage, this.page*this.perPage)
    },
    rlists(){
      return this.businessList.slice((this.page - 1)*this.perPage, this.page*this.perPage)
    }
  },
};
</script>

<style scoped>
span.border-s {
    border-bottom: 1px solid red;
    color: #0165a3;
}
.item.mitem {
  box-shadow: 0 6px 10px -6px rgba(0,0,0,0.5);
  background: #f1f2f380;
  padding: 7px;
}
.mimg {
  width: 110px;
}
.mtitle {
  font-weight: 600;
  font-size: 12.25px;
}
.mdetails {
  width: 100%;
  padding: 0px 0 0 7px;
}
.mcontact {
  width: 42px;
}
.fs11{font-size: 10.4px;}
.catside{
  background: #e5e5e5;
  padding: 10px;
}
.sec1{
  background: #f2f2f2;
  padding: 5px 15px;
  margin-bottom: 20px;
}
</style>
